nav{
    width: 100%;
    color: white;
    padding: 4px 0;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
}

.logo{
    width: 250px;
}

nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;
}

.dark_nav{
    background: #1f2531;
    transition: 0.5s ease-in-out;
}

.menu{
    display: none;
}


/* ----- media query ------- */

@media (max-width: 900px) {
    nav{
        padding: 15px 0;
    }
    nav ul{
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        background: #1f2531;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: 0.5s;
    }
    nav ul li{
        display: block;
        margin: 25px 40px;
        text-align: center;
    }
    .menu{
        display: block;
        cursor: pointer;
    }
    .hide-mobile-menu{
        right: -200px;
    }
}